.PopUp-button {
    display:block;
    width:80px;
    height:80px;
    line-height:80px;
    border: 2px solid #f5f5f5;
    border-radius: 50%;
    color:#f5f5f5;
    text-align:center;
    text-decoration:none;
    background: #555777;
    box-shadow: 0 0 3px gray;
    font-size:20px;
    font-weight:bold;
}
.PopUp-button:hover {
    background: #777555;
}
.PopUp-icon{
    color : gray;
}
.PopUp-icon:hover{
    cursor : pointer;
    color : lightgray;
}
.PopUp-image{
    height : 150px;
    width : 225px;
}